@-webkit-keyframes delete {
  from {
    left: 0%;
  }
  to {
    left: 100%;
    visibility: hidden;
  }
}

@keyframes delete {
  from {
    left: 0%;
  }
  to {
    left: 100%;
    visibility: hidden;
  }
}

.todoItem {
  background: #f1f2f6;
  position: relative;
  transition: background 0.2s ease-out;
}

.tododel {
  -webkit-animation: delete 0.6s;
          animation: delete 0.6s;
}

.todoItem:hover {
  background-color: #dfe4ea;
  cursor: pointer;
}

img {
  position: absolute;
  top: 15px;
  left: 50%;
  right: 50%;
}

.themeBar:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

